@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: url("../fonts/opensanslight.woff2") format("woff2"), url("../fonts/opensanslight.woff") format("woff");
}
:root {
  --special-blue-pale: hsla(211, 15%, 33%, 1);
  --basic-black: hsla(0, 0%, 0%, 1);
  --basic-white: hsla(0, 0%, 100%, 1);
  --basic-pink: hsla(344, 68%, 49%, 1);
  --special-blue: hsla(211, 27%, 21%, 1);
  --special-modal-shadow: rgba(0, 1, 1, 0.15);
  --dark-green: rgba(28, 155, 94);
  --header-min-height: 66px;
}
@media (min-width: 660px) {
  :root {
    --header-min-height: 110px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.footer-other-pages {
  text-align: center;
}

.footer-other__wrapper {
  display: flex;
  flex-flow: column wrap;
}

.footer-other__part {
  margin-bottom: 32px;
}

.footer__logo-tablet {
  color: #283645;
}

.footer__logo-tablet:hover {
  color: #d22856;
}

.footer__logo-tablet:active {
  color: #283645;
  opacity: 0.3;
}

.footer-other__logo-img {
  fill: currentColor;
}

.footer__logo-desktop {
  display: none;
}

.social {
  margin-bottom: 55px;
  display: flex;
  column-gap: 44px;
  padding-left: 70px;
  padding-top: 20px;
}

.social__link {
  width: 20px;
  height: 20px;
}

.social__link::before {
  display: block;
  content: "";
  width: 48px;
  height: 48px;
  transform: translate(-18px, -18px);
  border: 3px solid rgba(40, 54, 69, 0.1);
  border-radius: 50%;
}

.social__link:hover::before {
  border: 3px solid #283645;
}

.social__link:active::before {
  border: 3px solid rgba(40, 54, 69, 0.1);
}

.social__link:active {
  opacity: 0.3;
}

.social__link--tw {
  background-image: url("../img/icons/sprite.svg#twitter-pic");
  background-repeat: no-repeat;
}

.social__link--fb {
  background-image: url("../img/icons/sprite.svg#facebook-pic");
  background-repeat: no-repeat;
}

.social__link__yt {
  background-image: url("../img/icons/sprite.svg#youtube-pic");
  background-repeat: no-repeat;
}

.social__link__yt::before {
  transform: translate(-20px, -18px);
}

.copyright {
  display: flex;
  justify-content: center;
  align-items: center;
}

.copyright__text {
  padding-right: 15px;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
}

.copyright__link::before {
  display: block;
  content: "";
  width: 27px;
  height: 34px;
  background-image: url("../img/icons/sprite.svg#html-academy");
  background-repeat: no-repeat;
  background-position: center;
}

.copyright__link:hover::before {
  background-image: url("../img/icons/sprite.svg#html-academy-hover");
}

.copyright__link:active::before {
  background-image: url("../img/icons/sprite.svg#html-academy-active");
}

@media (min-width: 660px) {
  .footer-other__wrapper {
    display: grid;
    justify-content: space-between;
    justify-items: center;
    grid-template-columns: repeat(3, auto);
    grid-auto-flow: row;
    padding-bottom: 40px;
  }

  .footer-other__part {
    margin: auto;
  }

  .social {
    padding-top: 6px;
    padding-right: 81px;
  }

  .copyright {
    padding-top: 10px;
  }
}
@media (min-width: 980px) {
  .footer-other__wrapper {
    column-gap: 200px;
  }

  .footer__logo-desktop {
    display: block;
  }

  .footer__logo-tablet {
    display: none;
  }

  .footer__logo-desktop {
    color: #283645;
  }

  .footer__logo-desktop:hover {
    color: #d22856;
  }

  .footer__logo-desktop:active {
    color: #283645;
    opacity: 0.3;
  }

  .copyright__text {
    font-size: 18px;
  }

  .social {
    column-gap: 42px;
    padding-left: 12px;
    padding-right: 0;
    padding-top: 10px;
  }
}
.page-navigation,
.page-navigation--opened {
  --nav-bg: var(--special-blue);
  margin: 0;
  position: relative;
  padding-top: var(--header-min-height);
  background-color: var(--nav-bg);
  box-shadow: 30vw 0 0 0 var(--nav-bg), -30vw 0 0 0 var(--nav-bg);
}
.page-navigation::before,
.page-navigation--opened::before {
  content: var(--is-nav-visible, "");
  position: absolute;
  top: calc(var(--header-min-height) - 1px);
  width: 100%;
  height: 1px;
  background-color: var(--basic-black);
  box-shadow: 30vw 0 0 0 var(--basic-black), -30vw 0 0 0 var(--basic-black);
}
.page-navigation--closed,
.page-navigation--opened--closed {
  --is-nav-visible: none;
  --nav-bg: transparent;
}
.page-navigation--nojs,
.page-navigation--opened--nojs {
  --nav-position: static;
  --is-toggel-visible: none;
}

.page-navigation__toggle {
  position: absolute;
  top: 20px;
  right: 0;
  gap: 12px;
  width: 50px;
  height: 24px;
  padding: 0;
  cursor: pointer;
  border: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

.page-navigation--closed .page-navigation__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 50px;
  height: 4px;
  background-color: var(--basic-white);
  box-shadow: 0 10px 0 0 var(--basic-white), 0 20px 0 0 var(--basic-white);
  border-radius: 1px;
  z-index: 20;
}
.page-navigation--closed .page-navigation__toggle:hover::before {
  background-color: var(--basic-pink);
  box-shadow: 0 10px 0 0 var(--basic-pink), 0 20px 0 0 var(--basic-pink);
}
.page-navigation--closed .page-navigation__toggle:active::before {
  opacity: 0.3;
}

.page-navigation--opened .page-navigation__toggle::before, .page-navigation--opened .page-navigation__toggle::after {
  content: "";
  position: absolute;
  top: 10px;
  left: 23px;
  width: 30px;
  height: 3px;
  background-color: var(--basic-white);
}
.page-navigation--opened .page-navigation__toggle::before {
  transform: rotate(45deg);
}
.page-navigation--opened .page-navigation__toggle::after {
  transform: rotate(-45deg);
}
.page-navigation--opened .page-navigation__toggle:hover::before, .page-navigation--opened .page-navigation__toggle:hover::after {
  background-color: var(--basic-pink);
}
.page-navigation--opened .page-navigation__toggle:active::before, .page-navigation--opened .page-navigation__toggle:active::after {
  opacity: 0.3;
}

.page-navigation__list {
  display: var(--is-nav-visible);
  position: var(--nav-position, absolute);
  left: 0;
  right: 0;
  background-color: var(--nav-bg);
  box-shadow: 30vw 0 0 0 var(--nav-bg), -30vw 0 0 0 var(--nav-bg);
  margin: 0;
  padding: 0;
  list-style: none;
}

.page-navigation__item {
  display: grid;
}
.page-navigation__item::before, .page-navigation__item::after {
  content: "";
  height: 1px;
  width: 100%;
  background-color: var(--separator);
  box-shadow: 30vw 0 0 0 var(--separator), -30vw 0 0 0 var(--separator);
}
.page-navigation__item::before {
  --separator: var(--special-blue-pale);
}
.page-navigation__item::after {
  --separator: var(--basic-black);
}

.page-navigation__link {
  color: var(--basic-white);
  font-size: 18px;
  line-height: 32px;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  display: block;
  padding: 16px;
}
.page-navigation__link, .page-navigation__link:visited, .page-navigation__link:hover, .page-navigation__link:active {
  text-decoration: none;
  font-variant: inherit;
  font-family: inherit;
}
.page-navigation__link:hover {
  color: var(--basic-pink);
}
.page-navigation__link:active {
  color: var(--basic-pink);
  opacity: 0.3;
}
.page-navigation__link[aria-current=page] {
  text-decoration-line: underline;
  text-decoration-thickness: 2px;
  text-underline-offset: 13px;
}

@media (min-width: 660px) {
  .page-navigation__toggle {
    top: 43px;
  }
}
@media (min-width: 980px) {
  .page-navigation,
.page-navigation--opened {
    --nav-bg: transparent;
    display: flex;
    justify-content: flex-end;
  }
  .page-navigation::before,
.page-navigation--opened::before {
    content: initial;
  }

  .page-navigation__toggle {
    display: none;
  }

  .page-navigation__list {
    top: 22px;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    column-gap: 85px;
    background-color: transparent;
  }

  .page-navigation__item::before, .page-navigation__item::after {
    content: initial;
  }

  .page-navigation__link {
    font-weight: 300;
    text-transform: none;
    padding-inline: 0;
  }
  .page-navigation__link:hover {
    color: var(--basic-pink);
  }
  .page-navigation__link:active {
    opacity: 0.3;
  }
}
a {
  -webkit-tap-highlight-color: transparent;
}

.page {
  min-height: 100%;
  font-family: "Open Sans", "Arial", sans-serif;
}

.page__body {
  margin: 0;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: min-content 1fr min-content;
  min-height: 100vh;
  font-size: 14px;
  line-height: 24px;
}

.page__corrector {
  margin: 0 auto;
  width: 280px;
}
@media (max-width: 659.5px) {
  .page__corrector--except-mobile {
    display: contents;
  }
}
@media (min-width: 660px) {
  .page__corrector {
    width: 620px;
  }
}
@media (min-width: 980px) {
  .page__corrector {
    width: 940px;
  }
}

.page__modal {
  margin: auto;
  display: none;
  position: fixed;
  top: 25vh;
  right: 0;
  bottom: 0;
  left: 0;
  width: 320px;
  background-color: #ffffff;
  box-shadow: 0 7px 15px var(--special-modal-shadow);
}
@media (min-width: 660px) {
  .page__modal {
    width: 460px;
  }
}
@media (min-width: 980px) {
  .page__modal {
    width: 780px;
    height: 377px;
  }
}

.page__modal-show {
  display: block;
}

.page-header {
  margin-bottom: -69px;
  width: 100%;
  min-width: 280px;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 10;
}

.page-checkbox {
  display: none;
}

.page-header__wrapper {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 320px;
  padding-top: 7px;
  padding-bottom: 7px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
  box-sizing: border-box;
}
@media (min-width: 1200px) {
  .page-header__wrapper {
    box-sizing: content-box;
  }
}

.page-checkbox:checked ~ .page-header__wrapper {
  width: 100%;
  background-color: #283645;
  border-bottom: 1px solid #000000;
}

.page-checkbox:checked ~ .page-header__wrapper .page-nav {
  position: absolute;
  top: 70px;
  left: 0;
  display: block;
  margin: 0 auto;
  width: 100%;
  min-height: 69px;
  background-color: #283645;
}

.page-checkbox:not(:checked) ~ .page-header__wrapper .page-nav {
  display: none;
}

.page-header__logo {
  width: 150px;
  cursor: pointer;
  color: #ffffff;
}
.page-header__logo:hover {
  color: #d22856;
}
.page-header__logo:active {
  color: rgba(255, 255, 255, 0.3);
}

.page__logo-img {
  fill: currentColor;
}

.page__desktop {
  display: none;
}

.page__tablet {
  display: block;
  padding-bottom: 15px;
}
.page__tablet > .logo-glasses {
  display: none;
}
.page__tablet > .logo-word {
  transform: translateX(-70px);
}

.page-nav__toggle {
  display: flex;
  align-items: center;
  position: relative;
  width: 50px;
  height: 24px;
  padding-bottom: 3px;
  cursor: pointer;
  z-index: 1;
}

.page-nav__toggle > .page-nav__menu,
.page-nav__toggle > .page-nav__menu::before,
.page-nav__toggle > .page-nav__menu::after {
  display: block;
  position: absolute;
  right: 0;
  width: 100%;
  height: 4px;
  background-color: #ffffff;
}

.page-nav__toggle > .page-nav__menu::before {
  content: "";
  top: -10px;
}

.page-nav__toggle > .page-nav__menu::after {
  content: "";
  top: 10px;
}

.page-nav__menu:hover,
.page-nav__menu:hover::before,
.page-nav__menu:hover::after {
  background-color: #d22856;
}

.page-nav__menu:active,
.page-nav__menu:active::before,
.page-nav__menu:active::after {
  opacity: 0.3;
}

.page-checkbox:checked ~ .page-header__wrapper .page-nav__toggle .page-nav__menu {
  transform: rotate(45deg);
  width: 30px;
}

.page-checkbox:checked ~ .page-header__wrapper .page-nav__toggle .page-nav__menu::before {
  top: 0;
  transform: rotate(0);
}

.page-checkbox:checked ~ .page-header__wrapper .page-nav__toggle .page-nav__menu::after {
  top: 0;
  transform: rotate(90deg);
}

.page-nav__list {
  margin: 0;
  text-align: center;
  width: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
}

.page-nav__link {
  display: block;
  padding-top: 15px;
  padding-bottom: 15px;
  font-size: 18px;
  line-height: 32px;
  font-weight: 700;
  border-top: 1px solid rgba(255, 255, 255, 0.15);
  border-bottom: 1px solid #000000;
  text-decoration: none;
  text-transform: uppercase;
  color: #ffffff;
}
.page-nav__link, .page-nav__link:visited, .page-nav__link:hover, .page-nav__link:active {
  text-decoration: none;
  font-variant: inherit;
  font-family: inherit;
}
.page-nav__link:hover {
  color: #d22856;
}
.page-nav__link:active {
  opacity: 0.3;
}
.page-nav__link[aria-current=page] {
  text-decoration-line: underline;
  text-decoration-thickness: 2px;
  text-underline-offset: 13px;
}

@media (min-width: 660px) {
  .page-header {
    margin-bottom: -110px;
  }

