.reviews {
  padding-bottom: 41px;
  background-color: $color-white;
}

.reviews__wrapper {
  display: flex;
  flex-flow: column wrap;
  justify-content: center;
  align-items: center;
  text-align: center;

  @include wrapper;
}

.reviews__item {
  display: flex;
  flex-flow: column wrap;
}

.reviews__box {
  display: none;
}

.reviews__text {
  margin: 0 0 19px;
  font-size: 14px;
  line-height: 30px;
  font-weight: 400;
  order: 2;
}

.reviews__author-info {
  display: none;
  order: 3;
}

.reviews__author-name {
  order: 1;
  margin-bottom: 14px;
  font-size: 18px;
  line-height: 30px;
  font-weight: 700;
}

.reviews__toggles {
  order: 4;
  margin-bottom: 10px;
  padding-left: 9px;

  &:hover .reviews__toggle {
    background-color: $black-60;
  }

  &:active .reviews__toggle {
    background-color: $black-10;
  }
}

.reviews__toggle {
  margin-right: 12px;
  padding: 3px;
  background-color: $black-transparent;
  border: none;
  border-radius: 50%;
  cursor: pointer;

  &:nth-of-type(1) {
    background-color: $color-white;
    border: 3px solid $color-basic-black;
  }
}

.reviews__arrow {
  display: none;
}

@media (min-width: 660px) {
  .reviews {
    margin-bottom: 57px;
    padding: 0;
  }

  .reviews__wrapper {
    width: 660px;
  }

  .reviews__text {
    margin-bottom: 37px;
    font-size: 18px;
    order: 1;
  }

  .reviews__author-name {
    order: 2;
    font-size: 20px;
    margin-bottom: 2px;
  }

  .reviews__author-info {
    margin: 0 0 28px 0;
    display: block;
  }

  .reviews__toggles {
    margin: 0;
    padding-bottom: 0;
  }
}

@media (min-width: 1200px) {
  .reviews {
    margin-bottom: 95px;
    padding-top: 14px;
  }

  .reviews__wrapper {
    position: relative;
    width: 1200px;
    padding: 0 130px;
  }

  .reviews__text {
    margin-bottom: 38px;
    padding: 0 204px;
  }


  .reviews__box {
    margin-bottom: 41px;
    display: block;
  }

  .reviews__author-name {
    font-size: 24px;
    line-height: 30px;
  }

  .reviews__author-info {
    font-size: 18px;
    line-height: 30px;
    margin-bottom: 41px;
  }

  .reviews__arrow {
    display: block;
  }

  .reviews__left-arrow {
    position: absolute;
    top: 150px;
    left: 130px;
  }

  .reviews__left-arrow::before {
    content: "";
    position: absolute;
    left: 130px;
    top: -8px;
    width: 22px;
    height: 41px;
    background-image: url("../../img/icons/left-arrow.svg");
  }

  .reviews__left-arrow:hover::before {
    background-image: url("../../img/icons/left-arrow-60.svg");
  }

  .reviews__left-arrow:active::before {
    background-image: url("../../img/icons/left-arrow-10.svg");
  }

  .reviews__right-arrow {
    position: absolute;
    top: 150px;
    right: 130px;
  }

  .reviews__right-arrow::before {
    content: "";
    position: absolute;
    right: 130px;
    top: -8px;
    width: 22px;
    height: 41px;
    background-image: url("../../img/icons/right-arrow.svg");
  }

  .reviews__right-arrow:hover::before {
    background-image: url("../../img/icons/right-arrow-60.svg");
  }

  .reviews__right-arrow:active::before {
    background-image: url("../../img/icons/right-arrow-10.svg");
  }

  .reviews__toggles {
    display: none;
  }
}
